/* Project page styles - modern, centered, readable with animated background */

// Variables (kept local to this file)
$max-width: 100%;
// $accent-1: #6c5ce7; // purple
// $accent-2: #00b894; // green
// $text: #e6eef8;
 $muted: #9aa4b2;


@keyframes gradientShift {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

/* Container */
.project-page {
  max-width: $max-width;
  //background: rgba(255,255,255,0.02);
  //box-shadow: 0 8px 30px rgba(2,6,23,0.6);
  backdrop-filter: blur(6px) saturate(120%);
}

.project-header {
  position: relative; // Ajouté pour positionner la vidéo
  text-align: center;
  height: 40vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden; // Évite que la vidéo dépasse
}

.header-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover; // Équivalent de background-size: cover
  z-index: -2;
}

.header-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4); /* Assombrit la vidéo pour mieux lire le texte */
  z-index: -1;
}

.project-header h1 {
  font-size: 2.2rem;
  margin: 0 0 0.5rem 0;
}

.project-meta {
  
  font-size: 0.95rem;
  margin: 0 0 1rem 0;
}

.project-description {
  font-size: 1.05rem;
  max-width: 820px;
  margin: 0 auto;
}

.project-content {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  margin-top: 3rem;
  

}

article {
  margin-left: 1rem ;
  flex: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 2rem;
  background: $brand-color;
  border-radius: 50px;
}

aside {
 
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

aside section div {
  margin: 1.5rem;
}



/* === CAROUSEL GALLERY === */

.project-gallery {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  aspect-ratio: 16/9;
  margin-bottom: 4rem;
  width: 90%;
  height: auto;


}

.project-gallery h2 {
  margin-top: 0;
  margin-bottom: 1rem;
  font-size: 1.8rem;
  flex-shrink: 0;
}

.project-carousel {
  position: relative;
  width: 100%;
  aspect-ratio: 16/9;
  display: flex;
  flex-direction: column;
  min-height: 0;
  flex-shrink: 0;
}

/* Row container - Position relative pour les boutons absolus */
.project-carousel .row {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Viewport - zone visible */
.carousel-viewport {
  width: 100%;
  height: 100%;
  overflow: hidden;
  border-radius: 12px;
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

/* Track - container des slides */
.carousel-track {
  width: 100%;
  height: 100%;
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Individual slide */
.carousel-slide {
  display: none;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  padding: 0;
}

.carousel-slide.active {
  display: flex;
}

/* Images - proportions gardées, fit dans le cadre */
.carousel-slide img {
  max-width: 100%;
  max-height: 100%;
  width: auto;
  height: auto;
  object-fit: contain;
  display: block;
  border-radius: 8px;
}

/* Vidéos */
.carousel-slide .video-wrapper {
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.video-wrapper iframe {
  width: 100%;
  height: 100%;
  max-height: 100%;
  border-radius: 8px;
  border: none;
}


/* Navigation buttons - POSITIONNÉS SUR L'IMAGE */
.carousel-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: $background-color;
  color: white;
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 2rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 20;
  line-height: 1;
}

.carousel-btn.prev {
  left: -2.7rem;
}

.carousel-btn.next {
  right: -2.7rem;
}

.carousel-btn:hover {
  background: $brand-color;
  transform: translateY(-50%) scale(1.15);
}

.carousel-btn:active {
  transform: translateY(-50%) scale(1.05);
}

.carousel-btn:focus {
  outline: 2px solid $text-color;
  outline-offset: 3px;
}

/* Indicators */
.carousel-indicators {
  margin-top: 0.5rem;
  display: flex;
  justify-content: center;
  gap: 10px;
  padding: 0;
  flex-shrink: 0;
}

.indicator {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: $background-color;
  border: 2px solid transparent;
  cursor: pointer;
  transition: all 0.3s ease;
  padding: 0;
}

.indicator:hover {
  background: $brand-color;
  transform: scale(1.2);
}

.indicator.active {
  background: $projectbox-text;
  border-color: $text-color;
  transform: scale(1.3);
}

.indicator:focus {
  outline: 2px solid $text-color;
  outline-offset: 2px;
}

/* Vidéos - même logique que les images */
.carousel-slide .video-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.video-wrapper iframe {
  max-width: 100%;
  max-height: 100%;
  width: auto;
  height: auto;
  aspect-ratio: 16/9; /* Garde le ratio YouTube */
  border-radius: 8px;
  border: none;
}

/* Responsive Design */
@media (max-width: 768px) {
  .carousel-btn {
    width: 45px;
    height: 45px;
    font-size: 1.8rem;
  }
  
  .carousel-btn.prev {
    left: 1rem;
  }
  
  .carousel-btn.next {
    right: 1rem;
  }
}

@media (max-width: 480px) {
  .carousel-btn {
    width: 40px;
    height: 40px;
    font-size: 1.5rem;
  }
  
  .carousel-btn.prev {
    left: 0.5rem;
  }
  
  .carousel-btn.next {
    right: 0.5rem;
  }
  
  .indicator {
    width: 10px;
    height: 10px;
  }
}

